import React, { Component } from 'react';
import './App.css';

import people from './API_SP.POP.TOTL_DS2_en_xml_v2_10224853.xml';
import co2 from './API_EN.ATM.CO2E.KT_DS2_en_xml_v2_10227343.xml';

class App extends Component {
  render() {
    return (
      <Interface />
    );
  }
}

class Interface extends Component{
  constructor(){
    super();
    this.state ={
      selected: false,
      searchItem: "",
      population: null,
      emissions: null,
      textfield: "Select a country",
      matches: null,
      countries: [],
      selectedCountry: null,
      data: null,
      infobox: null
    }
    this.loadFile = this.loadFile.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.matchClick = this.matchClick.bind(this);
    
    
  }
  componentDidMount(){
   
    this.loadFile(people, true);
    this.loadFile(co2, false);
    
   
    
  }
  

  formData () {
   

    var countryMatches = [];
   
    
    for (var xx = 0; xx < this.state.countries.length; xx++){
      if (this.state.textfield.toLowerCase() === this.state.countries[xx].substring(0, this.state.textfield.length).toLowerCase() && this.state.textfield.length > 1){
        countryMatches.push(this.state.countries[xx]);
      }
    }
    
    var countryMatchList = <ul id="matchlist">{countryMatches.map((country) => 
    <li onClick={() => this.matchClick(country)}>{country} </li>)}</ul>;
    this.setState({matches: countryMatchList});
    
    
    }
    
  formTable(){
  try{
    
   
    var parser = new DOMParser();
    
    var people = parser.parseFromString(this.state.population, 'text/xml');
    var co2 = parser.parseFromString(this.state.emissions, 'text/xml');
    var numbers = [];
    var countries = people.getElementsByName("Country or Area");
    var year = people.getElementsByName("Year");
    var maxYear= 0;
    for (var i = 0; i < countries.length; i++){
      if(countries[i].textContent === this.state.selectedCountry){
        numbers.push(i);
        maxYear = year[i].textContent;
        }
    }
    var dataTable = [];

    for (var x = 0; x < numbers.length; x++){
      
      if(this.state.selected === true){
        dataTable.push(<tr><th>{maxYear - x }</th><th>{people.getElementsByName("Value")[numbers[numbers.length - x -1 ]].textContent}</th>
        <th>{co2.getElementsByName("Value")[numbers[numbers.length - x - 1]].textContent / people.getElementsByName("Value")[numbers[numbers.length - x - 1]].textContent}</th></tr>);
      }else{
        dataTable.push(<tr><th>{maxYear - x}</th><th>{people.getElementsByName("Value")[numbers[numbers.length - x -1]].textContent}</th>
        <th>{co2.getElementsByName("Value")[numbers[numbers.length - x - 1]].textContent}</th></tr>);
      }
     
    }
    var finalTable = <tbody>{dataTable}</tbody>
    this.setState({data: finalTable});
 
   
  }catch(e){
  
  }

  }  
 
 
  handleChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
   
    if(target.type === 'checkbox'){
      this.setState({
        [name]: value
        
      }, () =>this.formTable());
    }else{
      this.setState({
        [name]: value
        
      });
    }
    
    this.formData();
    
    
   
  }
 
  
  loadFile(filename, boolean){
    
    var rawFile = new XMLHttpRequest();
    var allText;
    rawFile.open("GET", filename);
    rawFile.onreadystatechange = function (){
      if(rawFile.readyState === 4){
        if(rawFile.status === 200 || rawFile.status === 0){
            allText = rawFile.responseText;
            var parser = new DOMParser();
            var parsed = parser.parseFromString(allText, 'text/xml');
            if (boolean === true){
              var countries = parsed.getElementsByName("Country or Area");
              var countryNames = [];
              for (var x = 0; x < countries.length; x++){
                if(countries[x].textContent !== countryNames[countryNames.length-1]){
                  countryNames.push(countries[x].textContent);
                }
              }
              this.setState({countries: countryNames});
              this.setState({population: allText});
            }else{
              this.setState({emissions: allText});
            }
        }
    }
}.bind(this);
  rawFile.send(null);

  }
  matchClick(country){
    this.setState({selectedCountry:country }, ()=> {this.formTable()});
  }
 
  
  handleSubmit(event){
    this.setState({selectedCountry: this.state.textfield.substring(0,1).toUpperCase() + 
      this.state.textfield.substring(1, this.state.textfield.length)}, () =>{this.formTable()});
    this.formTable();
    event.preventDefault();
  }

  render(){
    return(
      <div id="interface">
      <h1>CO2 Emissions</h1>
      <form onSubmit={this.handleSubmit}>
        <label>
          Country: 
          <input
            
            name="textfield"
            type="text"
            value={this.state.textfield}
            onChange={this.handleChange} /></label><button onClick={this.handleSubmit}>Search</button>
            <div>{this.state.matches}</div>
        
        
        <br />
        <label>
          Per Capita:
          <input
            name="selected"
            type="checkbox"
            checked={this.state.selected}
            onChange={this.handleChange} />
        </label>
      </form>
      <h2>{this.state.selectedCountry}</h2>
      <div id="data"><table><tr><th></th><th>Population</th><th>Emissions</th></tr>{this.state.data}</table></div>
      <div>{this.state.infobox}</div>
      <footer>
        <p>Jaakko Kittil√§ - 2019</p>
        <p>jaakko.kittila@gmail.com</p>
      </footer>
      </div>
      );
    }

}

export default App;
